@tailwind base;
@tailwind components;
@tailwind utilities;

/* Import fonts here */

.wp-block {
	max-width: 100%;
}

@layer base {
	body {
		@apply font-serif;
	}

	#tinymce {
		@apply p-5;
	}

	#tinymce:focus {
		@apply outline-none;
	}

	h1,
	.h1,
	h2,
	.h2,
	h3,
	.h3,
	h4,
	.h4,
	h5,
	.h5,
	h6,
	.h6 {
		@apply font-heading;
	}

	h1,
	.h1 {
		@apply text-4xl lg:text-6xl;
	}
	h2,
	.h2 {
		@apply text-4xl lg:text-5xl;
	}
	h3,
	.h3 {
		@apply text-3xl lg:text-4xl;
	}
	h4,
	.h4 {
		@apply text-2xl leading-tight lg:text-3xl;
	}
	h5,
	.h5 {
		@apply text-xl leading-tight lg:text-2xl;
	}

	.p1 {
		@apply lg:leading-135	 text-2xl lg:text-4xl;
	}

	.p2 {
		@apply text-xl leading-snug lg:text-2xl;
	}

	.p3 {
		@apply text-lg leading-snug lg:text-xl lg:leading-normal;
	}

	.p4 {
		@apply leading-snug lg:text-lg lg:leading-normal;
	}

	.p5 {
		@apply text-sm leading-snug lg:text-base lg:leading-normal;
	}

	.p6 {
		@apply text-s leading-snug lg:text-sm lg:leading-normal;
	}

	.p7 {
		@apply text-xs leading-snug lg:leading-normal;
	}

	.w-subhead {
		@apply text-base font-bold uppercase leading-6 tracking-widest lg:text-lg;
	}

	.subhead {
		@apply font-bold uppercase leading-snug tracking-widest;
	}

	.subhead-one {
		@apply subhead lg:text-lg;
	}

	.subhead-two {
		@apply subhead text-sm lg:text-base;
	}

	.subhead-three {
		@apply subhead text-s lg:text-sm;
	}

	/* Mailchimp form */
	form[name='mc-embedded-subscribe-form'] {
		@apply mx-auto flex max-w-screen-lg gap-7;
	}

	form[name='mc-embedded-subscribe-form'] label {
		@apply hidden;
	}

	form[name='mc-embedded-subscribe-form'] > div[class] {
		@apply flex-grow;
	}

	form[name='mc-embedded-subscribe-form'] input[type='submit'] {
		@apply w-1/4 flex-grow bg-base-blue text-white;
	}

	form[name='mc-embedded-subscribe-form'] > div[class] input {
		@apply block w-full;
	}

	input:not([type='submit']),
	select {
		@apply bg-white p-1;
	}
}

@layer components {
	@import './tailwind-components/modal';

	p a:not(.btn),
	.wysiwyg a:not(.btn) {
		@apply underline;
	}

	input[type='submit'],
	.btn {
		@apply relative z-10 inline-block bg-base-blue py-4 px-8 text-white;
		line-height: 1;
	}
	.btn--video {
		padding-left: 62px;
		position: relative;
	}
	.btn--video:before {
		content: '';
		position: absolute;
		top: 50%;
		left: 30px;
		transform: translateY(-50%);
		border-left: 16px solid #fff;
		border-top: 10px solid transparent;
		border-bottom: 10px solid transparent;
	}

	.block-badge {
		@apply absolute right-2 top-2 rounded-sm bg-blue-400 p-1 px-3 font-sans text-xs font-bold uppercase leading-none text-white;
	}

	.arrow-link {
		@apply inline-block pr-6 text-sm uppercase leading-none !no-underline after:ml-3 after:inline-block after:-translate-y-px after:text-base-red after:content-['→'];
	}

	.indented-tag {
		@apply text-sm uppercase leading-none before:mr-5 before:inline-block before:h-px before:w-24 before:align-middle before:content-[''];
	}

	.header-nav-items > .menu-item > a {
		@apply block pt-5 pb-5;
	}

	.icon {
		@apply inline-block w-5;
	}

	.icon > svg {
		@apply h-auto w-full;
	}

	.legal-nav-items > .menu-item + .menu-item {
		@apply pl-4;
	}

	.pxblock {
		@apply py-10;
	}

	.aspect-video > .iframe > iframe {
		@apply absolute top-0 h-full w-full object-cover object-center;
	}
}
